@import url(https://fonts.googleapis.com/css2?family=Titillium+Web:wght@300;700&display=swap);
.search .searchInputWrapper{position:relative;display:flex;width:100%;align-items:center;position:relative;height:62px;padding:1px 30px}.search .searchInput{height:60px;font-size:calc(10px + 3vmin);display:inline-block;border:none;outline:none;color:white;padding:3px;padding:0px 40px 0px 10px;width:0px;position:absolute;top:0;right:0;left:100%;background:none;z-index:2;transition:all 0.4s cubic-bezier(1, 1, 1, 1);cursor:pointer}.search .searchInput.show{border-bottom:1px solid #fff;width:calc(100% - 60px);z-index:6;cursor:text;left:30px;cursor:text}.search img.search{height:calc(10px + 3vmin);width:calc(10px + 3vmin);min-height:30px;min-width:30px;display:inline-block;text-indent:-10000px;border:none;position:absolute;top:50%;right:30px;z-index:2;cursor:pointer;transition:opacity 0.3s ease-in;transform:translateY(-50%)}.search img.search:hover{opacity:0.8}

.header{width:100%;position:fixed;z-index:100;background-color:#161515;top:0;box-shadow:2px 6px 6px #161515}.header .header-items{position:relative}.header .header-items .static{display:flex;align-items:center;position:absolute;left:calc(42px + 2vmin);top:50%;transform:translateY(-50%) scale(1.2);transition:all 0.3s cubic-bezier(0.785, 0.135, 0.15, 0.86);z-index:1}.header .header-items .static .backButton{height:calc(10px + 3vmin);width:calc(10px + 3vmin);width:calc(10px + 3vmin);margin-right:15px;cursor:pointer;transition:all 0.3s cubic-bezier(0.785, 0.135, 0.15, 0.86)}.header .header-items .static .currentPageTitle{color:white;font-size:calc(10px + 2vmin);cursor:pointer}@media (min-width: 767px){.header .header-items .static .currentPageTitle{font-weight:bold}}.header .header-items .static.hide{opacity:0;z-index:0;left:-100px}.header .header-items .header-search{-webkit-appearance:none;-moz-appearance:none;appearance:none;width:100%;padding:0 20px;padding-right:40px;background:inherit;color:#fff;height:5rem;font-size:4rem}

.layout{display:flex;flex-direction:column}.layout .layout-wrapper{padding:calc(36px + 62px) 30px 36px;background-color:#161515;height:100vh;font-size:calc(10px + 2vmin);overflow:auto}

.playCard{box-sizing:border-box;cursor:pointer}.playCard:hover .playCardImageContainer::after{content:'';position:absolute;transform:translate(-50%, -50%);top:calc(50% - 15px);left:50%;width:100%;height:100%;box-shadow:inset 0px 0px 40px 10px rgba(21,21,21,0.8)}.playCard:hover .playCardImage,.playCard:hover .playCardName{transform-origin:center;transform:scale(0.98) translateY(-15px)}.playCard .playCardImageContainer{position:relative;transition:all 0.3s ease-in-out;transform:translateZ(0);margin-bottom:calc(4vh - 20px)}.playCard .playCardImageContainer .playCardImage{width:100%;height:100%;max-height:calc(40vw - 10px);min-height:calc(40vw - 10px);transition:all 0.08s ease-in-out}.playCard .playCardName{color:#fff;white-space:nowrap;overflow:hidden;text-overflow:ellipsis;width:calc(30vw - 28px);transition:all 0.08s ease-in-out}.pulse{height:100%;width:100%;background:linear-gradient(-90deg, #727070 0%, #a5a3a3 50%, #727070 100%);background-size:400% 400%;-webkit-animation:pulse 1.2s ease-in-out infinite;animation:pulse 1.2s ease-in-out infinite}@-webkit-keyframes pulse{0%{background-position:0% 0%}100%{background-position:-135% 0%}}@keyframes pulse{0%{background-position:0% 0%}100%{background-position:-135% 0%}}.skelton{height:100%;display:flex;align-items:center;flex-direction:column}.skelton .text,.skelton .image{background-color:#f5f5f5}.skelton .image{min-height:calc(40vw - 8px);width:100%;margin-bottom:calc(4vh - 20px)}.skelton .text{width:75%;margin-right:auto;min-height:6%}

.listingPage{width:100%}.listingPage .listings{display:grid;grid-template-columns:repeat(3, 1fr);grid-column-gap:30px;grid-row-gap:90px;grid-auto-rows:calc(40vw + 15px);padding-bottom:60px}.listingPage .noResults{color:#fff;font-size:calc(10px + 2vmin)}

*{margin:0px;padding:0px;box-sizing:border-box}body{margin:0;font-family:'Titillium Web', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;box-sizing:border-box}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

