@import '../../styles/variables.scss';
.listingPage {
  width: 100%;
  .listings {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-column-gap: 30px;
    grid-row-gap: 90px;
    // grid-template-rows: repeat(3, minmax(calc(40vw + 15px), 1fr));
    grid-auto-rows: calc(40vw + 15px);
    padding-bottom: 60px;
  }
  .noResults {
    color: $font-color;
    font-size: $responsive-font-size;
  }
  
}